import React, { Component } from 'react';

class About extends Component {
    render() {
        return (
            <div className="about-content">
                <div className="content-header">About Me</div>
                <div className="content-body">
                    <p>
                        A fashion design student currently attending LaSalle College in Vancouver, BC, Canada.
                    </p>
                    <p>
                        I see fashion as a form of self-expression through style. I strive to create classic garments for the <b><i>modern casual romantic</i></b>â€”clothing that is feminine, elegant and at the same time functional. The clothes that I design must be comfortable because comfort is a necessity for feeling beautiful.
                    </p>
                    <p>
                        I believe that everyone is entitled to wear clothing that makes themselves feel good, feel giddy, and beautiful in their own skin; this is what I aspire to create.
                    </p>
                </div>
            </div>
        )
    }
}

export default About;


